import React, {useState, useEffect, useRef} from "react";
import {useHistory, useParams} from "react-router-dom";
import styled from "styled-components";
import './Detail.scss'

let Box = styled.div`
    padding : 20px;
`;

let Title = styled.h4`
    font-size : 25px;
    color : ${props => props.color}
`;

function Detail(props){
    // const {setStock} = props;
    let {id} = useParams();
    let history = useHistory();
    let data = props.shoes.find(item => item.id == id);
    const alertRef = useRef(null);
    let [alert, setAlert] = useState(true);

    useEffect(() => {
        let timer = setTimeout(()=>{
            // alertRef.current.style.display = "none";
            setAlert(false);
        }, 2000);

        return () => {
            clearTimeout();
        }
    },[]);

    return(
        <div className="container">
            <Box>
                <Title color={'navy'} className="red">Detail</Title>
            </Box>
            {
                alert?
                    <div className="my-alert_yellow" ref={alertRef}>
                        <p>재고가 얼마 남지 않았습니다.</p>
                    </div>
                    : null
            }
            {
                data ?
                    <div className="row">
                        <div className="col-md-6">
                            <img src={`https://codingapple1.github.io/shop/shoes${data.id+1}.jpg`} width="100%" />
                        </div>
                        <div className="col-md-6 mt-4">
                            <h4 className="pt-5">{data.title}</h4>
                            <p>{data.content}</p>
                            <p>{"₩" + data.price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</p>
                            {/*재고*/}
                            <Info stock={props.stock[id]} />
                            <button
                                className="btn btn-danger"
                                onClick={() => {
                                    let changedStock = [
                                        ...props.stock
                                    ];
                                    changedStock[id] -= 1;
                                    props.setStock(changedStock);
                                }}
                            >주문하기
                            </button>
                            <button
                                className="btn btn-secondary"
                                onClick={()=>{
                                    history.goBack();
                                }}
                            >뒤로가기</button>
                        </div>
                    </div>
                    : null
            }
        </div>
    )
}

function Info(props){
    return(
        <p>
            재고 : {props.stock}개
        </p>
    );
};


export default Detail;